@-webkit-keyframes slideDown {
  from {
    -webkit-transform: translate(-50%, -100%);
    transform: translate(-50%, -100%);
    opacity: 0; }
  to {
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    opacity: 1; } }

@keyframes slideDown {
  from {
    -webkit-transform: translate(-50%, -100%);
    transform: translate(-50%, -100%);
    opacity: 0; }
  to {
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    opacity: 1; } }

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.bn-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5000; }

.bn-modal-overlay .bn-modal {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  min-width: 300px;
  background-color: #ffffff;
  border-radius: 4px;
  overflow: hidden; }

.bn-modal-overlay .bn-modal.theme-alert, .bn-modal-overlay .bn-modal.theme-confirm {
  margin-top: -5%; }

.bn-modal-overlay .bn-modal.theme-iframe {
  width: 80%;
  height: calc(100vh - 100px); }

.bn-modal-overlay .bn-modal.animated {
  -webkit-animation-name: slideDown;
  animation-name: slideDown;
  -webkit-animation-duration: 500ms;
  animation-duration: 500ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both; }

.bn-modal-overlay .bn-modal .bn-modal-header {
  display: flex;
  align-items: center;
  padding: 12px 18px;
  background-color: #fafafa;
  border-bottom: solid 1px #eeeeee; }

.bn-modal-overlay .bn-modal .bn-modal-header h1 {
