  margin: 0;
  font-size: 1.25rem;
  font-weight: normal; }

.bn-modal-overlay .bn-modal .bn-modal-header .toolbox {
  flex: 1;
  text-align: right; }

.bn-modal-overlay .bn-modal .bn-modal-header .toolbox > a {
  display: inline-block;
  margin-left: 8px;
  width: 24px;
  height: 24px;
  line-height: 22px;
  text-align: center;
  border-radius: 50%;
  cursor: pointer; }

.bn-modal-overlay .bn-modal .bn-modal-header .toolbox > a:hover {
  background-color: #eeeeee; }

.bn-modal-overlay .bn-modal .bn-modal-body {
  padding: 18px;
  min-height: 40px;
  max-height: calc(100vh - 100px);
  overflow: auto; }

.bn-modal-overlay .bn-modal iframe.bn-modal-body {
  border: none;
  width: 100%;
  height: 100%;
  padding: 0; }

.bn-modal-overlay .bn-modal .bn-modal-footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 12px 18px; }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container {
  text-align: right; }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn {
  margin-bottom: 0;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375em 0.75em;
  font-size: 1em;
  line-height: 1.5;
  border-radius: 0.25em;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out; }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn:focus {
  outline: none; }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-default {
  color: #212529;
  background-color: #f8f9fa;
  border-color: #f8f9fa; }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-default:not(:disabled):active {
  color: #212529;
  background-color: #dae0e5;
  border-color: #d3d9df; }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-default:focus {
