.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-primary {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff; }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-primary:not(:disabled):active {
  color: #fff;
  background-color: #0062cc;
  border-color: #005cbf; }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-primary:focus {
  box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); }

.bn-modal-overlay .bn-modal .bn-modal-footer .actions-container button {
  margin-left: 8px;
  cursor: pointer; }

.bn-modal-overlay .bn-modal .bn-modal-footer .tip {
  flex: 1;
  font-size: 0.85rem; }

.bn-notification-container {
  position: fixed; }

.bn-notification-container.center {
  left: 50%;
  text-align: center;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%); }

.bn-notification-container.center.top {
  top: 0; }

.bn-notification-container.center.top .notification-item:first-child {
  border-radius: 0 0 4px 4px; }

.bn-notification-container.center.bottom {
  bottom: 0; }

.bn-notification-container.center.bottom .notification-item {
  border-radius: 4px 4px 0 0; }

.bn-notification-container.top {
  top: 15px; }

.bn-notification-container.top .notification-item {
  margin-bottom: 15px; }

.bn-notification-container.top .notification-item.notification-item-hidden {
  margin-bottom: 0; }

.bn-notification-container.bottom {
  bottom: 15px; }

.bn-notification-container.bottom .notification-item {
  margin-top: 15px; }

.bn-notification-container.bottom .notification-item.notification-item-hidden {
  margin-top: 0; }

.bn-notification-container.left {
  left: 0; }

.bn-notification-container.left .notification-item {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%); }

.bn-notification-container.left .notification-item.notification-item-show {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  margin-left: 15px; }

.bn-notification-container.left .notification-item.notification-item-hidden {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%); }

.bn-notification-container.right {
  right: 0; }

.bn-notification-container.right .notification-item {
  -webkit-transform: translateX(100%);
  transform: translateX(100%); }

.bn-notification-container.right .notification-item.notification-item-show {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  margin-right: 15px; }

.bn-notification-container.right .notification-item.notification-item-hidden {
  -webkit-transform: translateX(100%);
  transform: translateX(100%); }

.bn-notification-container.square .notification-item {
  border-radius: 0; }

.bn-notification-container .notification-item {
  position: relative;
  padding: 15px;
  color: #ffffff;
  border-radius: 4px;
  transition: all 1500ms cubic-bezier(0.175, 0.885, 0.32, 1.275); }

.bn-notification-container .notification-item.default {
  background-color: rgba(0, 0, 0, 0.8); }

.bn-notification-container .notification-item.error {
  background-color: rgba(254, 26, 0, 0.8); }

.bn-notification-container .notification-item.success {
  background-color: rgba(76, 175, 80, 0.8); }

.bn-notification-container .notification-item.warning {
  background-color: rgba(254, 189, 0, 0.8); }

.bn-notification-container .notification-item.notification-item-show {
  transition-duration: 300ms; }

.bn-notification-container .notification-item.notification-item-hidden {
  height: 0;
  padding-top: 0;
  padding-bottom: 0;
  overflow: hidden;
  opacity: 0; }

/* Loading component */
@-webkit-keyframes loading-element {
  0% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1); }
  50% {
    -webkit-transform: scaleY(1.5);
    transform: scaleY(1.5); }
  100% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1); } }

@keyframes loading-element {
  0% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1); }
  50% {
    -webkit-transform: scaleY(1.5);
    transform: scaleY(1.5); }
  100% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1); } }

.loading-element {
  display: inline-block;
  position: relative;
  margin-bottom: 5px; }

.loading-element div {
  display: inline-block;
  margin: 0 2px;
  width: 0.3em;
  height: 0.6em;
  background: #333;
  -webkit-animation: loading-element 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  animation: loading-element 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  border-radius: 1px; }

.loading-element div:nth-child(1) {
  -webkit-animation-delay: -0.24s;
  animation-delay: -0.24s; }

.loading-element div:nth-child(2) {
  -webkit-animation-delay: -0.12s;
  animation-delay: -0.12s; }

.loading-element div:nth-child(3) {
  -webkit-animation-delay: 0;
  animation-delay: 0; }

.bn-loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  display: grid;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 5100;
  border-radius: 3px; }

.bn-loading-overlay.inline {
  padding: 0.5em; }

.bn-loading-overlay.inline .bn-loading-container {
  margin: inherit;
  text-align: left; }

.bn-loading-overlay.inline .bn-loading-container .loading-text {
  display: inline-block;
  margin-left: 0.25em; }

.bn-loading-overlay .bn-loading-container {
  margin: auto;
  text-align: center; }

.bn-loading-overlay .bn-loading-container .bicon {
  position: relative;
  top: 0.1em;
  display: inline-block;
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  text-align: left;
  line-height: 0; }

.bn-loading-overlay .bn-loading-container .bicon.bicon-loading {
  width: 0.9em;
  height: 0.9em;
  border-radius: 50%;
  border-width: 2px;
  border-style: solid;
  border-color: rgba(221, 221, 221, 0.5);
  border-left-color: #333;
  -webkit-animation: rotate 1s infinite linear;
  animation: rotate 1s infinite linear; }

.bn-loading-overlay .bn-loading-container .loading-text {
  color: #333; }

.bn-loading-overlay .bn-loading-container.has-text .bicon.bicon-loading {
  background-color: #ffffff; }

body > .bn-loading-overlay {
  position: fixed;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.5); }

body > .bn-loading-overlay .bn-loading-container .bicon.bicon-loading {
  width: 4em;
  height: 4em;
  border-width: 4px; }

body > .bn-loading-overlay .bn-loading-container .loading-text {
  position: relative;
  margin-top: -3.2em;
  font-size: 0.8em; }
