.dialog-dark .bn-modal-overlay {
  color: #dedede;
  background-color: rgba(0, 0, 0, 0.5); }

.dialog-dark .bn-modal-overlay .bn-modal {
  background-color: #333333; }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-header {
  background-color: #404040;
  border-bottom-color: #4d4d4d; }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-header .toolbox > a {
  color: rgba(222, 222, 222, 0.5); }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-header .toolbox > a:hover {
  color: #dedede;
  background-color: #333333; }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-default {
  color: #dedede;
  background-color: #262626;
  border-color: #262626; }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-default:not(:disabled):active {
  color: #dedede;
  background-color: #0d0d0d;
  border-color: #404040; }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-default:focus {
  box-shadow: 0 0 0 0.2rem rgba(38, 38, 38, 0.5); }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-primary {
  color: #dedede;
  background-color: #ff0266;
  border-color: #ff0266; }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-primary:not(:disabled):hover, .dialog-dark .bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-primary:not(:disabled):active {
  background-color: #e8005c;
  border-color: #ce0051; }

.dialog-dark .bn-modal-overlay .bn-modal .bn-modal-footer .actions-container .btn-primary:focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 2, 102, 0.5); }

.dialog-dark .bn-notification-container .notification-item.default {
  background-color: rgba(51, 51, 51, 0.8); }

.dialog-dark .bn-notification-container .notification-item.error {
  background-color: rgba(220, 53, 69, 0.8); }

.dialog-dark .bn-notification-container .notification-item.success {
  background-color: rgba(40, 167, 69, 0.8); }

.dialog-dark .bn-notification-container .notification-item.warning {
  background-color: rgba(255, 193, 7, 0.8); }

.dialog-dark .loading-element div {
  background-color: #dedede; }

.dialog-dark .bn-loading-overlay {
  background-color: rgba(0, 0, 0, 0.4); }

.dialog-dark .bn-loading-overlay .bn-loading-container .bicon.bicon-loading {
  border-color: rgba(255, 2, 102, 0.5);
  border-left-color: #ff0266; }

.dialog-dark .bn-loading-overlay .bn-loading-container .loading-text {
  color: #dedede; }

.dialog-dark .bn-loading-overlay .bn-loading-container.has-text {
  color: #dedede; }

.dialog-dark .bn-loading-overlay .bn-loading-container.has-text .bicon.bicon-loading {
  background-color: #000000; }

.dialog-dark > .bn-loading-overlay {
  background-color: rgba(0, 0, 0, 0.5); }
